

// Configuration
@import "../../scss/bootstrap/functions";

// Variables
@import "hugo:vars";
$hb-scrollbar-width: 12px;
$hb-scrollbar-height: 12px;
$hb-scrollbar-track-bg: #f8f9fa;
$hb-scrollbar-thumb-bg: #909294;
$hb-scrollbar-corner-bg: #909294;


$hb-content-panel-bottom: 20px;


$hb-progress-bar-height: 2px;



$headings-margin-bottom: 0.75rem !default;


$hb-blog-sidebar: true;
$hb-blog-sidebar-position: start;
$hb-blog-sidebar-width: 0.35;
$hb-blog-sidebar-sticky: true;
$hb-blog-home-pinned-posts-pos: '';

// ToC
$hb-blog-toc-position: end;

$hb-back-to-top-bottom: 1rem;
$hb-back-to-top-end: 1rem;
$hb-back-to-top-animation: true;


$hb-header-sticky: true;
$hb-header-menus-alignment: '';
$hb-header-logo-bg: #712cf9;
$hb-top-offset: if($hb-header-sticky, 7.75rem, 24px);
$hb-header-breakpoint: 'lg';


$hb-blog-sidebar-profile-avatar-size: 100;

$hb-gallery-album-item-gap: 10px;
$hb-gallery-album-item-border-radius: 5px;

